import { Box, IconButton } from '@mui/material'
import { useEffect, useState } from 'react';
import { ImageDatas } from './ImageData';
import { ArrowLeft } from '../../core/global/Icons';
import { useHistory } from 'react-router-dom';

export const MyAlbum = ({match}) => {
    const [imgdata, setImgdata] = useState([])
    const [title, setTitle] = useState('')
    const [selectedImage, setSelectedImage] = useState(null);
    const history = useHistory()

    const handleImageClick = (image) => {
        setSelectedImage(image);
    };

    const handleClosePreview = () => {
        setSelectedImage(null);
    };

    useEffect(() => {
        const __init = async() => {
            let imgdata = await ImageDatas(match)
            setImgdata(imgdata)
            let year
            let months
            let month
            let data 
            if (match.params.year === '1') {
                year = 'My 1 year'
            }else{
                year = `My ${match.params.year} years`
            }
            if (match.params.months === '1') {
                month = ' 1 month'
                months = 'My 1 month'
            }else{
                month = ` ${match.params.months} months`
                months = `My ${match.params.months} months`
            }
            if (match.params.year !== '0') {
                data = year + month
            }else{
                data = months
            }
            setTitle(data)
        }
        __init()
    }, [])
  
    return (
        <Box width="100%" textAlign="center" pt="32px" px={2} className="grid-gallery-container">
            <Box width="100%" display="flex" justifyContent="flex-start">
                <IconButton onClick={()=>history.push('/netnet-album')}>
                    <ArrowLeft/>
                </IconButton>
            </Box>
            <Box component="h1" fontWeight={700} color="#165320">{title}</Box>  
            <Box className="grid">
                {imgdata.map((image, index) => (
                <img
                    key={index}
                    src={image}
                    alt={`Image ${index}`}
                    onClick={() => handleImageClick(image)}
                />
                ))}
            </Box>

            {selectedImage && (
                <Box className="preview-overlay" onClick={handleClosePreview}>
                    <Box className="preview-content">
                        <img src={selectedImage} alt="Selected" />
                    </Box>
                </Box>
            )}
        </Box>
    )
}