export const ImageDatas = (match) => {
    let data
    if (match.params.months === '1') {
        data = [
            zra1_1, zra1_2, zra1_3, zra1_4, zra1_5, zra1_6
        ]
    }else if(match.params.months === '2'){
        data = [
            zra2_1,zra2_2,zra2_3,zra2_4,zra2_5,zra2_6
        ]
    }else if(match.params.months === '3'){
        data = [
            zra3_1,zra3_2,zra3_3,zra3_4
        ]
    }else if(match.params.months === '4'){
        data = [
            zra4_1,zra4_2,zra4_3,zra4_4,zra4_5,zra4_6
        ]
    }else if(match.params.months === '5'){
        data = [
            zra5_1,zra5_2,zra5_3,zra5_4,zra5_5
        ]
    }else if(match.params.months === '6'){
        data = [
            zra6_1,zra6_2,zra6_3,zra6_4,zra6_5,zra6_6,zra6_7,zra6_8,zra6_9,zra6_10
        ]
    }else if(match.params.months === '7'){
        data = [
            zra7_1,zra7_2,zra7_3,zra7_4,zra7_5
        ]
    }else if(match.params.months === '8'){
        data = [
            zra8_1,zra8_2,zra8_3,zra8_4,zra8_5,zra8_6,zra8_7
        ]
    }else if(match.params.months === '9'){
        data = [
            zra9_1,zra9_2,zra9_3,zra9_4,zra9_5,zra9_6
        ]
    }else if(match.params.months === '10'){
        data = [
            zra10_1,zra10_2,zra10_3,zra10_4,zra10_5
        ]
    }

    return data
}